/* src/components/GameBoard.css */
.game-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .game-board {
    display: grid;
    grid-template-rows: repeat(5, 1fr);
    grid-template-columns: repeat(5, 1fr);
    gap: 5px;
  }
  
  .row {
    display: contents;
  }
  
  .cell {
    width: 60px;
    height: 60px;
    background-color: #333;
    border: 1px solid #555;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 16px;
    color: #ddd;
  }
  
  .cell.occupied {
    background-color: #007bff;
  }
  
  .cell.selected {
    border: 2px solid #ffcc00;
  }
  
  .cell.valid-move {
    background-color: #28a745;
    cursor: pointer;
  }
  
  .move-controls {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  
  