/* src/App.css */
body {
  font-family: Arial, sans-serif;
  background-color: #222;
  color: #fff;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.game-over {
  font-size: 24px;
  color: #28a745;
  margin-top: 20px;
}

button {
  margin: 5px;
  padding: 5px 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #0056b3;
}

button:disabled {
  background-color: #6c757d;
  cursor: not-allowed;
}
